import Vue from 'vue'
import Vuex, { Module } from 'vuex'
import { AxiosResponse, AxiosError } from 'axios'
import { Banner, BannersState, FormField, BannerForm } from '../models'
import service from '../client'
// import isAlpha from 'validator/lib/isAlpha'

Vue.use(Vuex as any)

const module: Module<BannersState, any> = {
  namespaced: true,
  state: {
    list: [],
    form: {
      data: [
        {
          name: 'isActive',
          value: true,
          validationRequired: false,
          isValid: true,
          isFocused: false,
          isOptional: false,
          errorType: null,
          errorMsg: null,
        },
        {
          name: 'file',
          value: null,
          validationRequired: true,
          isValid: false,
          isFocused: false,
          isOptional: false,
          errorType: null,
          errorMsg: null,
        },
        {
          name: 'newsId',
          value: null,
          validationRequired: true,
          isValid: false,
          isFocused: false,
          isOptional: false,
          errorType: null,
          errorMsg: null,
        },
        {
          name: 'pageType',
          value: null,
          validationRequired: true,
          isValid: false,
          isFocused: false,
          isOptional: false,
          errorType: null,
          errorMsg: null,
        },
        {
          name: 'sort',
          value: null,
          validationRequired: false,
          isValid: true,
          isFocused: false,
          isOptional: true,
          errorType: null,
          errorMsg: null,
        }
      ],
      validationIsShown: false,
      errors: [
        { type: 'empty', msg: 'Поле не должно быть пустым' },
        { type: 'default', msg: 'Недопустимое значение' }
      ]
    }
  },

  getters: {
    listSorted(state) {
      return state.list && state.list.sort((a, b) => {
        const keyA = a.sort
        const keyB = a.sort

        if (keyA > keyB) return 1
        else if (keyA < keyB) return -1
        else return 0
      })
    },
    // FORM GETTERS
    formIsActive: state => state.form.data.find(field => field.name === 'isActive'),
    formFile: state => state.form.data.find(field => field.name === 'file'),
    formNewsId: state => state.form.data.find(field => field.name === 'newsId'),
    formPageType: state => state.form.data.find(field => field.name === 'pageType'),
    formSort: state => state.form.data.find(field => field.name === 'sort'),
    formIsValid: (state) => {
      const required = state.form.data.filter(field => !field.isOptional)
      const validFields = required.filter(field => field.isValid)
      console.log(required, validFields)

      return required.length <= validFields.length
    },
    formData(state, getters) {
      const formData: BannerForm = Object.assign({})

      formData.isActive = getters.formIsActive.value && getters.formIsActive.value.toString() || ''
      formData.file = getters.formFile.value
      formData.newsId = getters.formNewsId.value && getters.formNewsId.value.toString() || ''
      formData.pageType = getters.formPageType && getters.formPageType.toString() || ''
      formData.sort = getters.formSort && getters.formSort.toString() || ''

      return formData
    },
    preparedFormData(state, getters) {
      const formData = new FormData()

      Object.keys(getters.formData).forEach(key => {
        let value = getters.formData[key]
        formData.append(key, value)
      })

      return formData
    }
  },

  mutations: {
    saveList: (state, payload: Banner[]) => state.list = payload,
    // FORM MUTATIONS
    updateField(state, {name, value}) {
      const field = state.form.data.find(field => field.name === name)
      field.value = value
      field.isValid = !!field.value
      field.errorType = !field.value && !field.isOptional && 'empty'
      field.errorMsg = field.errorType && state.form.errors.find(f => f.type === field.errorType).msg || ''
    },
    setValidationIsShown: (state, payload) => state.form.validationIsShown = payload,
    clearForm(state) {
      const fields = state.form.data
  
      fields.forEach(field => {
        field.value = field.name === 'isActive' ? true : null
        field.isValid = (field.isOptional || !field.validationRequired) ? true : false
        field.isFocused = false
        field.errorType = !field.value && !field.isOptional && 'empty'
        field.errorMsg = field.errorType && state.form.errors.find(f => f.type === field.errorType).msg || ''
      })

      state.form.validationIsShown = false
    },
    handleFormErrors(state, errors) {
      const names = Object.keys(errors)
      if (names && names.length) {
        for (const name of names) {
          const field = state.form.data.find(f => f.name === name)
          field.errorMsg = errors[name]
        }
      }
    }
  },

  actions: {
    async getList({commit}) {
      return new Promise((resolve, reject) => {
        service.get('api/v1/banners-list')
          .then((res: AxiosResponse<any>) => {
            while (!Array.isArray(res)) res = res.data

            commit('saveList', res)
            resolve()
          })
          .catch(error => {
            console.log(error.response)
            reject()
          })
      })
    },
    async createBanner({state, getters, commit}) {
      return new Promise((resolve, reject) => {
        const formIsValid = getters.formIsValid

        // if (!formIsValid) {
        //   commit('setValidationIsShown', true)
        //   reject()
        //   return
        // }

        const data = getters.preparedFormData
        // if (!data.sort.value) commit('updateField',{name: 'sort', value: state.list.length + 1})

        service.post('api/v1/banner-create', data, {headers: {'Content-Type': 'multipart/form-data'}})
          .then((res: AxiosResponse<any>) => {
            commit('setValidationIsShown', false)
            resolve()
          })
          .catch((error: AxiosError<any>) => {
            console.log(error.response)
            commit('setValidationIsShown', true)
            if (!!error.response.data) commit('handleFormErrors', error.response.data.errors)
            reject(error.response)
          })
      })
    }
  }
}

export default module
