















import { Vue, Component, Prop } from 'vue-property-decorator'
import { MsgBoxContent } from '../models'

@Component({
})

export default class MessageBox extends Vue {
  @Prop() content: MsgBoxContent
  @Prop() loading: boolean

  onCloseClick() {
    this.$emit('close')
  }
  onResetClick() {
    this.$emit('reset')
  }
}
