
















import { Vue, Component } from 'vue-property-decorator'
import { State, Getter, Action, Mutation, namespace } from 'vuex-class'
import { MsgBoxContent } from '../models'
import MsgBoxTools from '../mixins/msgBoxTools'
import FormCreate from '../components/FormCreate.vue'
import MessageBox from '../components/MessageBox.vue'

const bannersModule = namespace('./module/store')

@Component({
  components: {
    FormCreate,
    MessageBox
  }
})

export default class PageCreate extends MsgBoxTools {
  msgBoxIsShown: boolean = false
  msgBoxIsLoading: boolean = false
  msgBoxContent: MsgBoxContent = {
    title: 'Ошибка при отправке',
    msg: 'Не удалось отправить данные',
    loadBtn: 'Отправить повторно',
  }

  // get formIsValid() { return Store.getters.formIsValid }
  @bannersModule.Getter formIsValid

  @bannersModule.Mutation clearForm
  @bannersModule.Action createBanner

  submitForm() {
    // Store.dispatch('createBanner')
    this.createBanner()
      .catch(error => {
        console.log(error)
        if (this.formIsValid) {
          this.onErrorCatch()
        }
      })
  }
  // clearForm() { Store.commit('clearForm') }
  goToPageMain() { this.$router.push({ name: 'PageMain' }) }
}
