




























import { Vue, Component, Watch } from 'vue-property-decorator'
import trim from 'validator/lib/trim'
import { Banner, BannerForm, MsgBoxContent } from '../models'
import DragDrop from './DragDrop.vue'
import { namespace } from 'vuex-class'

const bannersModule = namespace('./module/store')

@Component({
  components: {
    DragDrop
  }
})

export default class FormCreate extends Vue {
  // get list() { return Store.getters.listSorted }
  @bannersModule.State('listSorted') list
  get maxSortBy() { return this.list.length }
  @bannersModule.State form

  // FORM FIELDS
  // get isActiveField() { return Store.getters.formIsActive }
  @bannersModule.Getter('formIsActive') isActiveField
  // get fileField() { return Store.getters.formFile }
  @bannersModule.Getter('formFile') fileField
  // get newsIdField() { return Store.getters.formNewsId }
  @bannersModule.Getter('formNewsId') newsIdField
  // get pageTypeField() { return Store.getters.formPageType }
  @bannersModule.Getter('formPageType') pageTypeField
  // get sortField() { return Store.getters.formSort }
  @bannersModule.Getter('formSort') sortField

  @bannersModule.Mutation clearForm
  @bannersModule.Mutation setValidationIsShown
  @bannersModule.Mutation updateField

  // FORM SET/GET FIELDS VALUES
  get isActive() { return this.isActiveField.value }
  set isActive(value) { this.updateField({name: 'isActive', value}) }
  get newsId() { return this.newsIdField.value }
  set newsId(value) { this.updateField({name: 'newsId', value: trim(value)}) }
  get pageType() { return this.pageTypeField.value }
  set pageType(value) { this.updateField({name: 'pageType', value: trim(value)}) }
  get sortBy() { return this.sortField.value }
  set sortBy(value) { this.updateField({name: 'sort', value}) }

  created() {
    // Store.commit('clearForm')
    this.clearForm()
  }

  beforeDestroy() {
    // Store.commit('setValidationIsShown', false)
    this.setValidationIsShown(false)
  }

  // FORM METHODS
  isInvalid(field) { return this.form.validationIsShown && field.validationRequired && !field.isValid }
  // updateField({name, value}) { return Store.commit('updateField', {name, value}) }
}
